.boton__agregar{
    background-color: $boton;
    color: $blanco;
    padding: 1rem 2rem;
    position: fixed;
    right: 5%;
    bottom: 10%;
    border: none;
    border-radius: 10px;
    z-index: 10;
    font-size: 1.3rem;
    &:hover{
        background-color: rgb(1, 99, 1);
    }
}

.boton__borrar{
    background-color: red;
    border: none;
    color: $blanco;
    font-size: 1.3rem;
    border-radius: 10px;
    padding: 0.3rem 0;
    margin-top: -1rem;
    cursor: pointer;
    &:hover{
        background-color: rgb(85, 8, 8);
    }
}
