.tarea__contenedor{
    box-shadow: 1px 1px 5px black;
    margin: 1rem;
    border: 0.2px solid black;
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    cursor: pointer;
    transition: transform .2s;
    &:hover{
    transform: scale(1.1);
    }
}
.tarea__titulo{
    font-size: 2rem;
}
.tarea__nivel{
    font-weight: 100;
    font-size: 1.2rem;
}
.tarea__nivel-texto{
    font-weight: bold;
    color: red;
}
.tarea__texto{}
.tarea__creada{
    font-weight: 100;
    font-size: 1.2rem;
}
.tarea__creada-fecha{
    font-weight: bold;
    font-size: 1.2rem;
    color: green;
}