.form{
    display: flex;
    flex-direction: column;
    gap: 1.4rem;
    padding: 0.5rem 1rem 0.5rem 0;
    // background-color: rgba(139, 139, 135, 0.664);
    // height: 100%;
}
.form__titulo{
    margin: 0;
    color: $boton;
    text-align: center;
}
.form__pregunta{
    display: flex;
    flex-direction: column;
}
// .form__label{}
.form__input{
    font-size: 1.3rem;
    border: none;
    outline: none;
    padding: 0.3rem 1rem;
    border-radius: 10px;
    resize: none;
}

.form__boton{
    background-color: $boton;
    border: none;
    color: $blanco;
    font-size: 1.3rem;
    border-radius: 10px;
    padding: 0.3rem 0;
    cursor: pointer;
    &:hover{
        background-color: rgb(0, 83, 0);
    }
}