*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.auth__fondo{
    background-image: url($imagen);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
}
.auth__login{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 100vh;

}
.auth__titulo{
    text-align: center;
}
.auth__contenedor{
    background-color: black;
    padding: 4rem;
    color: $blanco;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 1rem;
    width: 100%;
    height: 100vh;


    @include pc() {
        border-radius: 20px;
        height: 85vh;
        background-color: $contenedor;
        width: 30%;
}

}

.auth__preg{
    display: flex;
    flex-direction: column;
    gap: 0.3rem;
}

.auth__input{
    border: none;
    outline: none;
    font-size: 1.4rem;
    padding: 0.3rem 0.6rem;
    border-radius: 5px;
}

.auth__label{
    color: $labels;
    text-transform: capitalize;
}

.auth__boton{
    background-color: $boton;
    border: none;
    color: $blanco;
    padding: 0.3rem 0;
    font-size: 1.3rem;
    border-radius: 5px;
    margin-top: 2rem;
    cursor: pointer;
    transition: background-color 0.3s;
    &:hover{
        transform: scale(1.02);
        background-color: rgb(3, 94, 3);
    }
}

.auth__texto-esp{
    text-align: center;
    color: $enlace;
    // font-weight: bold;
    font-size: 1.1rem;
    cursor: pointer;
    text-decoration: none;
}

.auth__texto{
    text-align: center;
}